import React, {useState} from 'react';
import Video from '../../videos/funny-workout.mp4';
import { Button } from '../Button.element';
import {
    HeroContainer,
    HeroBg,
    VideoBg,
    HeroContent,
    HeroTitle,
    HeroMsj,
    NavBtnLink,
    HeroBtnWrapper,
    ArrowForward,
    ArrowRight,
    
} from './Hero.elements';

const Hero = () => {
    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }
 


    return (
        <HeroContainer>
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='vieo/mp4'/>
            </HeroBg>
            <HeroContent>
                <HeroTitle>
                    Todo lo que necesitás para entrenar en un solo lugar
                </HeroTitle>
                <HeroMsj>
                    ¿ y si entrenar fuese tan atrapante como ver una serie ?
                </HeroMsj>
                <HeroBtnWrapper>
                    <NavBtnLink to="continuar" onMouseEnter={onHover} onMouseLeave={onHover}>PROBALO GRATIS {hover ? <ArrowForward /> : <ArrowRight />}</NavBtnLink>
                </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>

    )
}

export default Hero