import React, {useState, useEffect} from 'react' //runed with abreb 'rafce'
import { FaBars, FaTimes} from 'react-icons/fa';
import { IconContext } from 'react-icons/lib';
import { animateScroll as scroll } from 'react-scroll';
import { 
    Nav,
    NavbarContainer,
    NavLogo,
    NavIcon,
    NavIconName,
    MobileIcon,
    NavMenu,
    NavItem,
    NavLinks,
    NavItemBtn,
    NavBtnLink,   
} from './Navbar.elements';

const Navbar = ({toggle}) => {
    const [click, setClick] = useState(false)
    const [button, setButton] = useState(true);
    const [scrollNav, setScrollNav] = useState(false);
    const changeNav = () => {
        if(window.scrollY >= 495) {setScrollNav(true);} else {setScrollNav(false);}
        }

    useEffect(() => {window.addEventListener('scroll', changeNav);}, []);

    const handleClick = () => setClick(!click)

    const showButton = () => {
        if(window.innerWidth <= 960) {
            setButton(false)
        } else {
            setButton(true)
        }
    };

    useEffect(() => {
        showButton()
    }, []);

    window.addEventListener('resize', showButton);

    const toggleHome = () => {
        scroll.scrollToTop();  
    }

    return (
        <>
            <IconContext.Provider value={{color: '#fff'}}>
                <Nav scrollNav={scrollNav}>
                    <NavbarContainer>
                        <NavLogo to="/" onClick={toggleHome}>
                            <NavIcon/> <NavIconName />
                        </NavLogo>
                        <MobileIcon onClick={toggle}>
                            <FaBars />
                        </MobileIcon>
                        <NavMenu>
                            <NavItem>
                                <NavLinks to='features'
                                smooth={true} duration={500} spy={true} exact='true' offset={-60} >¿QUÉ OFRECEMOS?</NavLinks>
                            </NavItem>
                            <NavItem>
                                <NavLinks to='pricing'
                                smooth={true} duration={500} spy={true} exact='true' offset={-80}>SUSCRIPCIÓN</NavLinks>
                            </NavItem>
                            <NavItem>
                                <NavLinks to='equipo'
                                smooth={true} duration={500} spy={true} exact='true' offset={-80}>NUESTRO EQUIPO</NavLinks>
                            </NavItem>
                        </NavMenu>
                        <NavItemBtn>
                            <NavBtnLink to="continuar">Continuar</NavBtnLink>
                        </NavItemBtn>
                    </NavbarContainer>
                </Nav>
            </IconContext.Provider>
        </>
    );
};

export default Navbar
