import React, {useState} from 'react';
import CardBg from '../../images/CardBg.png';
import {
    PricingContainer,
    PricingBg,
    ImgBg,
    PricingContent, 
    PricingButton,
    ArrowForward,
    ArrowRight,
} from './Pricing.elements';

const Pricing = () => {
    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }

    return (
        <>
            <PricingContainer id='pricing'>
                <PricingBg>
                    <ImgBg src={CardBg} type='img/png'/>
                </PricingBg>
                <PricingContent>
                    <h1>La mejor opción para lograr tus objetivos</h1>
                    <p>Accedé a entrenamientos ilimitados, dónde sea y cuándo sea</p>
                    <h2>$499 x mes</h2>
                    <PricingButton to="continuar" onMouseEnter={onHover} onMouseLeave={onHover}>empezar mi prueba gratuita {hover ? <ArrowForward /> : <ArrowRight />}</PricingButton>
                </PricingContent>
            </PricingContainer>
        </>
    )
}

export default Pricing;
