import styled from 'styled-components';

export const Button = styled.button`
    border-radius: 10px;
    background: ${({primary}) => (primary ? '#010606' : '#9D43FF90') };
    white-space: nowrap;
    padding: ${({big}) => (big ? '12px 48px' : '10px 30px')};
    color: #ffffff;
    font-size: ${({fontBig}) => (fontBig ? '1.125rem' : '0.875rem')};
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    border: none;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-out;
        background: ${({primary}) => (primary ? '#ffffff' : '#C300EB99') };
        border: none;
        box-shadow: -1px -1px 5px #9D43FF50, 1px 1px 5px #9D43FF50;
    };
    
`;