import React from 'react';
import {
    FaFacebookSquare,
    FaInstagram,
    FaYoutube,
    FaTwitter,
} from 'react-icons/fa';
import {
    FooterContainer,
    FooterWrap,
    SocialMedia,
    SocialMediaWrap,
    SocialLogo,
    SocialIcons,
    SocialIconLink,
} from './Footer.elements';

const Footer = () => {
    return (
        <>
            <FooterContainer>
                <FooterWrap>
                    <SocialMedia>
                        <SocialMediaWrap>
                            <SocialLogo to='/'>powerup © 2020</SocialLogo>
                            <SocialIcons>
                                <SocialIconLink href='https://www.instagram.com/powerup.ar/' target='_blank' aria-label='Instagram'>
                                    <FaInstagram />
                                </SocialIconLink>
                                <SocialIconLink href='https://youtube.com/channel/UCrS0-vIPKb7EReErXFyTzhw' target='_blank' aria-label='Youtube'>
                                    <FaYoutube />
                                </SocialIconLink>
                                <SocialIconLink href='https://twitter.com/PowerUp24060339' target='_blank' aria-label='Facebook'>
                                    <FaTwitter />
                                </SocialIconLink>
                                <SocialIconLink href='https://www.facebook.com/PowerUp-114779743758327/' target='_blank' aria-label='Facebook'>
                                    <FaFacebookSquare />
                                </SocialIconLink>
                            </SocialIcons>
                        </SocialMediaWrap>
                    </SocialMedia>
                </FooterWrap>
                </FooterContainer>            
        </>
    )
}

export default Footer;