import React from 'react';
/* ver como omitir el picture-in-picture btn promt en opera */
import Video from '../../videos/teamwork.mp4';
import { getCookie, base_url} from '../../utils';
import { withRouter } from 'react-router-dom';
import { toast } from 'react-toastify';
import {
    Container,
    ContinuarBg,
    VideoBg,
    FormWrap,
    Icon,
    NavIcon,
    NavIconName,
    FormContent,
    Form,
    FormH1,
    FormLabel,
    FormInput,
    FormBtn,
    Text,
} from './Continuar.elements';


class Continuar extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            activeItem:{
                id:null,
                email:'',
            },
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
        this.cookie = this.cookie.bind(this)
    };

    cookie = getCookie;

    handleChange(e){
        var name = e.target.name
        var value = e.target.value
        console.log('Name:', name, 'value:', value)
    
        this.setState({
            activeItem:{
                ...this.state.activeItem,
                email:value,
            }
        })
    }

    handleSubmit(e){
        e.preventDefault()
        console.log('ITEM:', this.state.activeItem)
        var csrftoken = this.cookie('csrftoken')
        var slug = 'email-save/'
        var url = base_url  + slug

        console.log('url:', url)

        fetch(url, {
            method: 'POST',
            headers:{ 'Content-type':'application/json', 'X-CSRFToken':csrftoken, },
            body:JSON.stringify(this.state.activeItem),
        }).then((response) => {
            this.setState({
              activeItem:{
                id:null,
                email:'',
              }
            })
          }).catch(function(error){
            console.log('ERROR', error)
          })
        this.props.history.push('');
        toast.dark("¡Gracias por tu apoyo!");
    };

    render(){

        return (
            <>
                <Container>
                    <Icon to="/">
                            <NavIcon/><NavIconName/>
                    </Icon>
                    <ContinuarBg>
                        <VideoBg autoPlay loop muted src={Video} type='vieo/mp4'/>
                    </ContinuarBg>
                    <FormWrap>
                        
                        <FormContent>
                            <Form  onSubmit={this.handleSubmit} id='form' action="#">
                                <FormH1>Estamos trabajando para traerte tu mejor compañero de entrenamiento</FormH1>
                                <Text>Si estas interesado en saber más, no dudes en contactarnos</Text>
                                
                                <FormInput onChange={this.handleChange} id='email' value={this.state.activeItem.title}  name='email' type='email' required placeholder="Tu email" />
                                <FormBtn id='submit' type="submit">Conectar</FormBtn>
                            </Form>
                        </FormContent>
                    </FormWrap>
                </Container>
            </>
        )
    }
}

export default withRouter(Continuar);
