import styled from 'styled-components';
import { MdArrowForward, MdKeyboardArrowRight } from 'react-icons/md';
import {Link as LinkR} from 'react-router-dom';

export const HeroContainer = styled.div`
    background: #00ff0040;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 100vh;
    position: relative;
    z-index: 1;

    :before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.3) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%);
        z-index: 2;
    }
`

export const HeroBg = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
`

export const VideoBg = styled.video`
    width: 100%;
    height: 100%;
    --o-object-fit: cover;
    object-fit: cover;
    background: #232a34;
`

export const HeroContent = styled.div`
    z-index: 3;
    max-width: 1200px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    flex-direction: column;
    align-items: left;
`

export const HeroTitle = styled.h1`
    color: #ffffff;
    font-size: 1.5rem;
    text-align:left;
    font-weight: normal;
    padding: 0;

    @media screen and (max-width: 768px) {
        font-size: 1.5rem; 
    }

    @media screen and (max-width: 480px) {
        font-size: 1.125rem; 
    }
`

export const HeroMsj = styled.p`
    margin-top: 10px;
    color: #ffffff;
    font-size: 3rem; 
    max-width: 700px;
    font-weight: bold;
    text-transform: uppercase;

    @media screen and (max-width: 768px) {
        font-size: 2.5rem; 
    }

    @media screen and (max-width: 480px) {
        font-size: 2rem; 
    }

`

export const HeroBtnWrapper = styled.div`
    margin-top: 25px;
    font-size: 1.25rem;
    max-width: 300px;
`
export const NavBtnLink = styled(LinkR)`
    border-radius: 10px;
    background: ${({primary}) => (primary ? '#010606' : '#9D43FF90') };
    white-space: nowrap;
    padding: ${({big}) => (big ? '12px 48px' : '10px 30px')};
    color: #ffffff;
    font-size: ${({fontBig}) => (fontBig ? '1.125rem' : '0.875rem')};
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    border: none;
    text-decoration: none;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-out;
        background: ${({primary}) => (primary ? '#ffffff' : '#C300EB99') };
        border: none;
        box-shadow: -1px -1px 5px #9D43FF50, 1px 1px 5px #9D43FF50;
    };
`;

export const ArrowForward = styled(MdArrowForward)`
    margin-left: 20px;
    font-size: 1.25rem;
`

export const ArrowRight = styled(MdKeyboardArrowRight)`
    margin-left: 20px;
    font-size: 1.25rem;
`