import React from 'react';
import {
    FaLinkedin,
    FaInstagram,
} from 'react-icons/fa';
import {
    SiGmail,
} from 'react-icons/si'
import {
    CardsContainer,
    SectionHeading,
    CardsWrapper,
    CardBg,
    CardImg,
    CardName,
    CardInfo,
    CardDesc,
    SocialIcons,
    SocialIconLink,
} from './TeamCards.elements'

const TeamCards = ({ heading, data }) => {
    return (
        <>
            <CardsContainer id='equipo'>
                <SectionHeading>{heading}</SectionHeading>
                <CardsWrapper>
                    {data.map((card, index) => {
                    return (
                        <CardBg key={index}>
                            <CardImg src={card.img} alt={card.alt} />
                            <CardInfo>
                                <CardName>{card.name}</CardName>
                                <CardDesc>{card.desc}</CardDesc>
                                <SocialIcons>
                                    <SocialIconLink href={card.linkedin} target='_blank' aria-label='Linkedin'>
                                        <FaLinkedin />
                                    </SocialIconLink>
                                    <SocialIconLink href={card.instagram} target='_blank' aria-label='Instagram'>
                                        <FaInstagram />
                                    </SocialIconLink>
                                    <SocialIconLink href={card.email} target='_blank' aria-label='E-mail'>
                                        <SiGmail />
                                    </SocialIconLink>
                                </SocialIcons>
                            </CardInfo>
                        </CardBg>
                    );
                    })}
                </CardsWrapper>
            </CardsContainer>
        </>
    )
}

export default TeamCards;
