import styled from 'styled-components';
import { FaMagento } from 'react-icons/fa';
import {ReactComponent as Icon} from '../../static/images/Icon.svg';
import {ReactComponent as Name} from '../../static/images/Logo.svg';
import {Link as LinkR} from 'react-router-dom';
import {Link as LinkS} from 'react-scroll';
import { Container } from '../../globalStyles';

export const Nav = styled.nav`
    background: ${({ scrollNav }) => (scrollNav ? '#151216':'linear-gradient(to bottom, #151216 0%, transparent 100%)')};
    height: 80px;
    margin-top: -81px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: sticky;
    top: 0;
    z-index: 10;

    @media screen and (max-width: 768px){
        transition: all 0.8s ease;
    }
`;

export const NavbarContainer = styled(Container)`
    display: flex;
    justify-content: space-between;
    height: 80px;
    padding: 0 24px;
    ${Container} //take other values from Container
`;

export const NavLogo = styled(LinkR)`
    color: #fff;
    justify-self: flex-start;
    text-decoration: none;
    display: flex;
    align-items: center;
    cursor: pointer;
`;

export const NavIcon = styled( Icon )`
    width: 35px;
`;

export const NavIconName = styled( Name )`
    margin-right: 0.5rem;
    height: 20px;
    width: 90px;
`;

export const MobileIcon = styled.div`
    display: none;

    @media screen and (max-width: 960px){
        color: #ffffff;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 75%);
        font-size: 1.8rem;
        cursor: pointer;
    }
`;

export const NavMenu = styled.ul`
    display:flex;
    align-items: center;
    list-style: none;
    text-align: center;

    @media screen and (max-width: 960px){
        display: none;
    }
`;

export const NavItem = styled.li`
    height: 80px;

    @media screen and (max-width: 960px){
        width: 100%;
    }
`;

export const NavLinks = styled(LinkS)`
    color: #ffffff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    font-size: 0.875rem;
    cursor: pointer;

    &.active {
        border-bottom: 3px solid #AB54FD;
        margin-bottom: 3px;
    }

    &:hover {
        transition: all 0.2s ease-out;
        transform: scale(1.075);
    }
    
    @media screen and (max-width: 960px){
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        &:hover {
            transition: all 0.3s ease-out;
            text-decoration: none;
            transform: none;
        } 
    }
`;

export const NavItemBtn = styled.nav `
    display: flex;
    align-items: center;

    @media screen and (max-width: 960px) {
        display: none;
    }
`;

export const NavBtnLink = styled(LinkR)`
    border-radius: 10px;
    border: 2px solid #ffffff;
    background: transparent;
    white-space: nowrap;
    padding: ${({big}) => (big ? '12px 64px' : '10px 30px')};
    color: #ffffff;
    text-decoration: none;
    font-size: 0.875rem;
    outline: none;
    transition: all 0.2s ease-in-out;
    text-transform: uppercase;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-in-out;
        border-color: transparent;
        background: linear-gradient(135deg, #EF60FF 0%, #714AFB 100%);
        box-shadow: -1px -1px 4px #EF60FF50, 1px 1px 4px #714AFB;
        transform: scale(1.05);
    };
`;