import styled from 'styled-components';
import {ReactComponent as BrandIcon} from '../../static/images/Icon.svg';
import {ReactComponent as Name} from '../../static/images/Logo.svg';
import { Link } from 'react-router-dom';

export const Container = styled.div`    
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    z-index: 0;
    background: linear-gradient(-135deg, #EF60FF90 0%, #714AFB90 100%);
`

export const ContinuarBg = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
`

export const VideoBg = styled.video`
    width: 100%;
    height: 100%;
    --o-object-fit: cover;
    object-fit: cover;
    opacity: 30%;
`

export const FormWrap = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media screen and (max-width: 400px) {
        height: 80%;
    }
`

export const Icon = styled(Link)`
    position: absolute;
    left: 0;
    height: 80px;
    margin-left: 24px;
    top: 0;
    color: #fff;
    text-decoration: none;
    display: flex;
    align-items: center;
    z-index: 1;
    cursor: pointer;

    @media screen and (max-width: 480px) {
        margin-top: 8px;
        margin-left: 16px;
    }
`

export const NavIcon = styled( BrandIcon )`
    height: 100%;
    width: 35px;
`;

export const NavIconName = styled( Name )`
    margin-right: 0.5rem;
    height: 20px;
    width: 90px;
`;

export const FormContent = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    @media screen and (max-width: 480px) {
        padding: 10px;
    }

`

export const Form = styled.form`
    background: #151216;
    max-width: 400px;
    height: auto;
    width: 100%;
    z-index: 1;
    display: grid;
    margin: 0 auto;
    padding: 45px 35px;
    border-radius: 15px;

    @media screen and (max-width: 400px) {
        padding: 32px 32px;
    }
`

export const FormH1 = styled.h1`
    margin-bottom: 20px;
    color: #ffffff;
    font-size: 1.25rem;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
`

export const FormLabel = styled.label`
    margin-top: 10px;
    font-size: 0.875rem;
    color: #ffffff;
`

export const FormInput = styled.input`
    padding: 16px 16px;
    background: #ffffff10;
    margin-top: 15px;
    margin-bottom: 35px;
    border: 1px solid transparent;
    border-radius: 15px;
    color: #ffffff;

    &:focus {
        outline: none !important;
        border:1px solid #AB54FD;
    }
`

export const FormBtn = styled.button`
    border-radius: 10px;
    border: 2px solid #ffffff;
    background: transparent;
    padding: 16px 0; 
    color: #ffffff;
    font-size: 0.875rem;
    outline: none;
    transition: all 0.2s ease-in-out;
    text-transform: uppercase;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-in-out;
        border-color: transparent;
        background: linear-gradient(135deg, #EF60FF 0%, #714AFB 100%);
        box-shadow: -1px -1px 4px #EF60FF50, 1px 1px 4px #714AFB;
        transform: scale(1.005);
    };
`

export const Text = styled.span`
    text-align: center;
    margin-top: 15px;
    color: #ffffff;
    font-size: 0.875rem;
`
