import styled from 'styled-components';
import { MdArrowForward, MdKeyboardArrowRight } from 'react-icons/md';
import {Link as LinkR} from 'react-router-dom';

export const PricingContainer = styled.div`
    background: #151216;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 45px 40px;
    height: 100vh;
    position: relative;

    @media screen and (max-width: 768px) {
        margin: 35px 20px;
        margin-top: 250px;
    }

    @media screen and (max-width: 400px) {
        margin-top: 0;
    }
`

export const PricingBg = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 40px;
    
    @media screen and (max-width: 768px) {
        border-radius: 25px;
    }
`

export const ImgBg = styled.img`
    width: 100%;
    height: 100%;
    --o-object-fit: cover;
    object-fit: cover;
`

export const PricingContent = styled.div`
    max-width: 1200px;
    width: 100%;
    position: absolute;
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: left;
    color: #ffffff;
    
    @media screen and (max-width: 768px) {
        padding: 20px 50px;
    }

    h1 {
        font-size: clamp(1rem, 2vw, 1.5rem);
        font-weight: normal;
        margin-bottom: 20px;

        @media screen and (max-width: 768px) {
        font-size: 1rem;
        margin-bottom: 20px;
        }

        @media screen and (max-width: 400px) {
        font-size: 1rem;
        margin-bottom: 20px;
        }
    }
    
    p {
        font-size: clamp(2rem, 5vw, 3rem);
        text-transform: uppercase;
        font-weight: bold;
        margin-bottom: 10px;

        @media screen and (max-width: 768px) {
            font-size: 2rem;
            margin-bottom: 10px;
        }

        @media screen and (max-width: 400px) {
            font-size: 1rem;
            margin-bottom: 10px;
        }
    }

    h2{
        font-size: clamp(0.9rem, 2vw, 1.2rem);
        font-weight: normal;
        margin-bottom: 30px;

        @media screen and (max-width: 768px) {
            font-size: 1rem;
            margin-bottom: 100px;
        }

        @media screen and (max-width: 400px) {
            font-size: 1rem;
            margin-bottom: 100px;
        }
    }
`

export const PricingButton = styled(LinkR)`
    border-radius: 10px;
    max-width: 400px;
    background: transparent;
    white-space: nowrap;
    padding: ${({big}) => (big ? '12px 48px' : '10px 30px')};
    color: #ffffff;
    font-size: ${({fontBig}) => (fontBig ? '1.125rem' : '0.875rem')};
    font-weight: bold;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    border: solid 2px;
    border-color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-out;
        background: ${({primary}) => (primary ? '#ffffff' : '#ffffff90') };
        color: #2c2c2c;
        border-color: transparent;
    };

    @media screen and (max-width: 400px) {
        font-size: 0.6rem;
    }
`

export const ArrowForward = styled(MdArrowForward)`
    margin-left: 20px;
    font-size: 1.25rem;
`

export const ArrowRight = styled(MdKeyboardArrowRight)`
    margin-left: 20px;
    font-size: 1.25rem;
`
