import React from 'react';
import GlobalStyle from './globalStyles'
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import { 
  Homepage,
  PaginaContinuar,
} from './pages';

function App() {
  return (
    <Router>
      <GlobalStyle />
      <Switch>
        <Route path="/" component={Homepage} exact/>
        <Route path="/continuar" component={PaginaContinuar} exact />
      </Switch>
    </Router>
  );
};

export default App;
